 header{
  width: 100%;
  height: 1000px;
  .sm(height, 400px);
  .bg("../img/header_bg.png");
  .header_top{
    padding-top: 40px;
    .sm(padding-top, 20px);
    .navbar{
      display: flex;
      justify-content: space-between;
      align-items: center;
       .nav_bar{
         border: 2px solid @gold;
         background-color: transparent;
         padding: 5px 10px;
         color: @gold;
         border-radius: 5px;
         box-shadow: 2px 2px 10px black;
         display: none;
         .lg(display, block);
       }
       .navbars{
         display: block;
        .lg-block({
          display: none;
          margin: auto;
        });
       }
  
      .logo{
        text-decoration: none;
        text-transform: capitalize;
        color: white;
        font-weight: 400;
        font-size: 34px;
        display: flex;
        align-items: center;
        visibility: visible ;
        &:hover{
          color: @gold;
          text-shadow: 2px 2px 2px black;
        }      
      }
      .navs{
        display: flex;
        justify-content: space-between;
        align-items: center;
        .sm-block({
          display: block;
        });
        
        li{
          margin: 0px 15px;
          .lg-block({
            margin: 0px 12px;
          });
          .sm-block({
            margin: 5px 5px;
            
          });
          a{
            text-decoration: none;
            text-transform: capitalize;
            font-weight: 300;
            font-size: 14px;
            color: white;
            &:hover{
              color: @gold;
              text-shadow: 2px 2px 2px black;
            }  
          }
        }
      }
      .form_search{
        border: 2px solid @gold;
        background: rgba(0,0,0, .5);
        padding: 40px;
        box-shadow: 2px 2px 24px black;
        z-index: 1111;
        position: absolute;
        right: 40%;
        top: -1000px;
        i{
          display: block;
          text-align: right;
          transform: translate( 41px , -43px);
          color: @gold;
          font-size: 25px;
        }
        input{
          display: block;
          padding: 10px 50px 10px 20px;
          border: 2px solid @gold;
          background: transparent;
          font-size: 15px;
          color: white;
          text-transform: capitalize;
        }
        .search{
          padding: 15px 50px;
          margin: auto;
          margin-top: 50px;
          display: block;
        }
      }

    }
  }
  .owl-carousel{
    height: 100%;
    .owl-stage-outer{
      height: 100%;
    }
    .owl-next{
      position: absolute;
      top: 32%;
      right: 10%;
      .sm(display, none);
      span{
      font-size: 90px;
      color: white;
      }
    }
    .owl-prev{
      position: absolute;
      top: 32%;
      left: 10%;
      .sm(display, none);
      span{
      font-size: 90px;
      color: white;
      }
    }
    .owl-dots{
      position: absolute;
      bottom: 130px;
      width: 100%;
      text-align: center;
      .owl-dot{
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background-color: #8f9092;
        margin-right: 10px;
      }
    }
  }
  .header_title{
    text-align: center;
    color: black;
    position: relative;
    background-color: rgba(139,207,53,0.2);
    height: 100%;
    .header_text{
      position: absolute;
      top: 300px;
      width: 100%;
      .sm(top, 40px);
      h2{
        color: white;
        font-weight: 500;
        font-size: 36px;
        text-transform: capitalize;
        text-align: center;
        .sm(font-size, 20px);
      }
      h1{
        color: white;
        text-transform: capitalize;
        font-size: 52px;
        font-weight: 800;
        text-align: center;
        margin-bottom: 30px;
        margin-top: 20px;
        .sm(font-size, 30px);
        .sm(margin-bottom, 10px);
        .sm(margin-top, 10px);
      }
      p{
        color: white;
        font-weight: 300;
        font-size: 16px;
        text-transform: capitalize;
        .sm(font-size, 12px);
      }
      .start{
        margin-top: 50px;
        .sm(display, none);
      }
    }

   
  }
 

 
 }