footer {
  .footer_top {
    background: url(../img/footbg.png) no-repeat center center / cover;
    padding: 130px 0px;
    .md-block({
      padding: 50px 0px;
    })
  }

  .footer_middle {
    background-color: black;
    padding-top: 80px;
    .logos {
      margin-bottom: 30px;
      .logo {
        text-decoration: none;
        text-transform: capitalize;
        color: white;
        font-weight: 400;
        font-size: 34px;
        display: flex;
        align-items: center;
        &:hover {
          color: @gold;
          text-shadow: 2px 2px 2px black;
        }
      }
    }
    p {
      color: #555555;
      font-weight: 300;
      font-size: 14px;
      text-align: left;
      line-height: 30px;
      span {
        display: block;
        text-transform: capitalize;
        color: #363636;
        font-size: 12px;
      }
    }
    .read_more {
      margin-top: 60px;
      color: #d1d1d1;
      font-size: 14px;
      font-weight: 400;
      display: block;
      text-transform: capitalize;
      i {
        margin-left: 20px;
        text-decoration: none;
      }
    }
    h3 {
      color: white;
      font-weight: 700;
      font-size: 24px;
      text-transform: capitalize;
      margin-bottom: 30px;
    }
    .light {
      color: @gold;
        margin-top: 40px;
      border-top: 1px solid #252525;
      border-bottom: 1px solid #252525;
      padding-bottom: 20px;
      span{
          color: white;
          padding-top: 20px;
      }
    }
    .waxom {
      display: inline-block;
      color: #00bff3;
      font-size: 14px;
      font-weight: 300;
    }
    .bit {
      display: inline-block;
      color: white;
      font-size: 14px;
      font-weight: 300;
    }
    .border{
      border-top: 1px solid #252525;
      border-bottom: 1px solid #252525;
      padding-bottom: 10px;
      margin-top: 10px;
    }
    img{
        margin: 5px;
    }

  }

  .footer_bottom{
    background-color: black;
    padding-top: 100px;
    padding-bottom: 50px;
    .sm-block({
      padding: 50px 0px;
    });
    p{
        color: #555555;
        font-weight: 300;
        font-size: 13px;
        text-transform: capitalize;
        border-right: 2px solid #555555;
        padding-right: 10px;
        span{
            color: @gold;
        }
    }
    .left{
        display: flex;
        align-items: center;
        
        ul{
            display: flex;
            li{
                padding: 0px 15px;
                border-right: 2px solid #555555;
                a{
                    text-decoration: none;
                    text-transform: capitalize;
                    color: #555555;
                    font-weight: 300;
                    font-size: 13px;
                    &:hover{
                        color: @gold;
                    }
                }
            }
        }
    }
    .right{
        display: flex;
        align-items: center;
        p{
            padding-left: 10px;
            span{
                color: white;
            }
        }
    }

  }
}
