@import "./header.less";
@import "./section.less";
@import "./params/var.less";
@import "./params/mix.less";
@import "./footer.less";

img {
  max-width: 100%;
  height: auto;
}

body {
  font-family: Lato;
}

.box_img {
  width: 100%;
  height: auto;
}

.btn {
  color: white;
  padding: 20px 40px;
  text-transform: uppercase;
  background-color: #998675;
  border: none;
  border-radius: 10px;
  font-size: 14px;
  font-weight: 900;
}

.active {
  color: @gold;
}

.click {
  border: 2px solid @gold;
  padding: 10px;
  background-color: transparent;
  border-radius: 50%;
  font-size: 20px;
  position: fixed;
  top: 300px;
  right: 100px;
  z-index: 1111;
  color: @gold;
  box-shadow: 2px 2px 10px black;
  display: none;
  .sm(right, 30px);
}
.fluid{
  .lg(max-width, 100%);
  .sm(max-width, 100%);
}

.t-center{
  text-align: center;
}