 section{
     .section_first{
         margin-top: 80px;
         .md(margin-top, 50px);
         .sm(margin-top, 30px);
         .svg{
             text-align: center;
             svg{
                width: 50px;
                height: 50px;
                text-align: center;
                
            }
         }
        h3{
            font-size: 22px;
            font-weight: 600;
            color: #555555;
            text-transform: capitalize;
            margin-top: 30px;
            margin-bottom: 20px;
            text-align: center;
        }
        p{
            font-size: 14px;
            font-weight: 300;
            line-height: 30px;
            color: #8c8c8c;
            text-align: center;
        }
     }
     .section_second{
         margin-top: 80px;
         .md(margin-top, 50px);
         .sm(margin-top, 30px);
         h2{
             text-align: center;
             text-transform: capitalize;
             font-weight: 600;
             font-size: 30px;
             color: #555555;
             margin-bottom: 30px;
             .md(margin-top, 20px);
             .sm(margin-top, 10px);
         }
         p{
             text-align: center;
             text-transform: capitalize;
             font-weight: 300;
             font-size: 16px;
             color: #8c8c8c;
             line-height: 30px;
             .md(text-align, justify);
             .sm(text-align, justify);
         }
         .btn_project{
             padding-top: 40px;
             padding-bottom: 40px;
             text-align: center;

             .btn_tab{
                 padding: 10px 25px;
                 text-transform: capitalize;
                 font-size: 14px;
                 font-weight: 300;
                 color: #8c8c8c;
                 border: 2px solid #ebebeb;
                 border-radius: 5px;
                 background: transparent;
                 margin: 0px 5px;
                 .sm-block({
                     display: block;
                    width: 100%;
                     margin: auto;
                     margin-bottom: 20px;
                 });   
                 &:hover{
                    background: @gold;
                    border: none;
                    color: white;
                    box-shadow: 2px 2px 10px black;
                 }
             }
         }
        .project_item{
            position: relative;
            transition: 500ms;
            &:hover .project_info{
              background-color: #362f2d;
              color: white;
              h3{
                  color: white;
              }
              p{
                  color: white;
              }
            }
            &:hover .icons_position{
                display: flex;
                align-items: center;
                justify-content: center;
            }
            .project_info{
                padding-left: 30px;
                h3{
                    font-size: 20px;
                    font-weight: 500;
                    color: #c7b299;
                    padding-top: 30px;
                    padding-bottom: 15px;
                    text-transform: capitalize;
                }
                p{
                    font-size: 14px;
                    font-weight: 300;
                    color: #8c8c8c;
                    text-transform: capitalize;
                    text-align: left;
                    padding-bottom: 25px;
                    line-height: 0px;
                }
            }
            .icons_position{
                position: absolute;
                top: 0;
                width: 100%;
                height: 100%;
                background: rgba(0,0,0, .7);
                display: none;
                i{
                    margin: 0px 15px;
                    font-size: 20px;
                    color: white;
                }
            }
        }
        .load{
            margin: auto;
            display: block;
        }

     }
     .mb_30{
         margin-bottom: 30px;
     }
     .section_three{
         margin-top: 80px;
         height: auto;
         width: 100%;
         background: url(../img/bg.png) no-repeat center center    ;
         padding: 100px 0px;
         .md(margin-top, 50px);
         .sm(margin-top, 30px);
     }
     .section_four{
         margin-top: 80px;
         .lg(padding-bottom, 40px);
         .md(margin-top, 50px);
         .sm(margin-top, 30px);
         h3{
             font-size: 30px;
             font-weight: 600;
             color: #555555;
             margin-top: 90px;
             margin-bottom: 40px;
             text-transform: capitalize;
             .lg-block({
                 margin-top: 0px;
                 margin-bottom: 20px;
                 font-size: 25px;
             });
         }
         p{
             font-weight: 300;
             font-size: 16px;
             color: #8c8c8c;
             line-height: 30px;
             text-align: justify;
         }
         .mobile_text{
             margin-top: 40px;
             padding-left: 50px;
             .lg(margin-top, 20px);
             .lg(padding-left, 0px);
             p{
                 font-weight: 300;
                 font-size: 16px;
                 color: #8c8c8c;
                 text-transform: capitalize;
                 margin-bottom: 10px;
                 i{
                     margin-right: 30px;
                     background: @gold;
                     padding: 5px;
                     border-radius: 50%;
                 }
             }
         }
     }
     .section_five{
         padding: 70px 0px 60px;
         background: #3f2b22;
         width: 100%;
         height: auto;
         .md(margin-top, 50px);
         .sm(margin-top, 30px);
         .counter_icon{
             i{
                 font-size: 40px;
                 color: @gold;
                 margin-bottom: 30px;
                 text-align: center;
                 display: block;
             }
         }
         .counter_texts{
            .number{
                 color: white;
                 font-size: 48px;
                 font-weight: 200;
                 margin-bottom: 20px;
                 text-align: center;
             }
             .text{
                 text-transform: uppercase;
                 text-align: center;
                 font-size: 14px;
                 font-weight: 300;
                 color: #c7b299;  
             
                &::after{
                    content: "";
                    display: block;
                    width: 30px;
                    height: 2px;
                    background:white;                  
                    text-align: center;
                    margin: auto;
                    margin-top: 30px;

                }
             }
         }

     }
     .section_six{
         margin-top: 80px;
         .md(margin-top, 50px);
         .sm(display, none);
         h2{
             font-weight: 600;
             font-size: 30px;
             color: #555555;
             text-transform: capitalize;
             margin-bottom: 30px;
             text-align: center;
         }
         p{
             font-weight: 300;
             font-size: 16px;
             color: #8c8c8c;
             line-height: 0px;
             text-align: center;
            margin-bottom: 30px;
         }
         .owl-carousel{
             .project_box{
                 
                 &:hover .project_box_item{
                     background-color: #362f2d;
                     h3{
                         color: @gold !important;
                     }
                     p{
                         color: white !important;
                     }
                     a{
                         color: @gold;
                     }
                 }
                 .project_box_item{
                     .project_box_info{
                         padding-left: 30px;
                         h3{
                             font-weight: 500;
                             font-size: 20px;
                             color: #555555;
                             text-transform: capitalize;
                             margin-bottom: 20px;
                             margin-top: 30px;
                         }
                         p{
                            font-weight: 300;
                            font-size: 14px;
                            color: #8c8c8c;
                            line-height: 30px;
                            margin-bottom: 0;
                            text-align: left;
                         }
                         a{
                             color: @gold;
                             font-size: 14px;
                             font-weight: 300;
                             text-transform: capitalize;
                             text-decoration: none;
                             margin-top: 30px;
                             display: block;
                             padding-bottom: 30px;
                             i{
                                 margin-left: 20px;
                             }
                         }
                       
                     }
                  
                 }
             }
             .owl-nav{
                 text-align: center;
                 font-size: 18px;
                 margin-top: 45px;
                 margin-bottom: 80px;
                 .owl-prev{
                    padding: 6px !important;
                     border: 2px solid @gold;
                     margin-right: 10px;
                     border-radius: 50%;
                   
                 }
                 .owl-next{
                    padding: 6px !important;
                     border: 2px solid @gold;
                     margin-right: 20px;
                     border-radius: 50%;
                   
                 }
             }
         }
     }
    .fluid{
        .lg(max-width, 100%);
        
    }
    .col_4{
        background: #000;
        width: 380px;
        height: 380px;
        transform: rotate(130deg);
    }
    .col_6{
        background: red;
       width: 100%;
       height: 80px;
    }


